import { useState, useEffect, Fragment } from "react";
import axios from "axios";

import AddService from "./AddService";

export default function ServiceList() {
  const [post, setPost] = useState(false);
  const [updatePost, setUpdatePost] = useState(false);
  const [newData, setNewData] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [errorMsg, setErrorMsg] = useState();

  const [latestPost, setLatestPost] = useState({
    title: "",
    icon: "",
    image: "",
    description: "",
  });

  const getPost = async () => {
    setIsLoading(true);
    try {
      await axios
        .get(`${process.env.REACT_APP_API_URL}/service`)
        .then((res) => setPost(res.data));
    } catch (err) {
      setErrorMsg(err.message || "Something went wrong");
    }
    setIsLoading(false);
  };

  const deletePost = async (id) => {
    try {
      await axios.delete(`${process.env.REACT_APP_API_URL}/service/${id}`);
      getPost();
    } catch (err) {
      console.log(err.message);
    }
  };

  useEffect(() => {
    getPost();
  }, []);

  const editPost = (data) => {
    setLatestPost(data);
    setUpdatePost(true);
    setNewData(true);
  };

  return (
    <Fragment>
      <div className="card" id="card">
        <div className="card-header">Service List</div>
        <div className="card-body">
          {isLoading && (
            <div className="alert alert-primary mt-3" role="alert">
              Loading post ...
            </div>
          )}
          {errorMsg && (
            <div className="alert alert-danger mt-3" role="alert">
              {errorMsg}
            </div>
          )}

          {post && (
            <div className="row">
              {post.map((list) => (
                <div className="col-4 mb-4" key={list.id}>
                  <div className="card">
                    <div className="card-header d-flex align-items-center justify-content-between ps-3">
                      {list.title}{" "}
                      <div className="action-group">
                        <button
                          type="button"
                          className="btn btn-light"
                          onClick={() => {
                            editPost({ ...list });
                          }}
                        >
                          <i className="bi bi-pencil-square"></i>
                        </button>
                        <button
                          type="button"
                          className="btn btn-light"
                          onClick={() => {
                            deletePost(list.id);
                          }}
                        >
                          <i className="bi bi-x-lg"></i>
                        </button>
                      </div>
                    </div>
                    <div className="card-body">
                      <p dangerouslySetInnerHTML={{ __html: list.icon }}></p>
                      {list.image && (
                        <figure className="image-box">
                          <img
                            src={process.env.REACT_APP_ASSET_URL + list.image}
                            alt=""
                          />
                        </figure>
                      )}
                      {list.description}
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
          {newData && (
            <AddService
              editData={latestPost}
              updatePost={updatePost}
              isActive={(state) => {
                setNewData(state);
              }}
              refreshPost={getPost}
            />
          )}

          {!newData && !isLoading && (
            <button className="btn btn-info" onClick={() => setNewData(true)}>
              Add new
            </button>
          )}
        </div>
      </div>
    </Fragment>
  );
}
