import { useState } from "react";
import axios from "axios";

import ImageUpload from "../ImageUpload";

export default function AddService({
  updatePost,
  refreshPost,
  isActive,
  editData,
}) {
  //For form data
  const [title, setTitle] = useState(editData.title);
  const [icon, setIcon] = useState(editData.icon);
  const [image, setImage] = useState(editData.image);
  const [description, setDescription] = useState(editData.description);

  //For custom states
  const [isLoading, setIsLoading] = useState(false);
  const [errorMsg, setErrorMsg] = useState();

  const resetFormData = () => {
    setTitle("");
    setIcon("");
    setImage("");
    setDescription("");
  };

  const addServiceData = async () => {
    setIsLoading(true);
    const formData = new FormData();
    formData.append("title", title);
    formData.append("icon", icon);
    formData.append("image", image);
    formData.append("description", description);
    try {
      await axios
        .post(`${process.env.REACT_APP_API_URL}/service`, formData)
        .then(() => refreshPost());
    } catch (err) {
      setErrorMsg(err.message + " please try again" || "Something went wrong");
    }
    setIsLoading(false);
  };

  const editPost = async (id) => {
    try {
      await axios
        .patch(`${process.env.REACT_APP_API_URL}/service/${id}`, {
          title,
          icon,
          image,
          description,
        })
        .then(() => refreshPost());
    } catch (err) {
      console.log(err.message);
    }
  };

  const submit = (event) => {
    event.preventDefault();

    if (updatePost) {
      editPost(editData.id);
    } else {
      addServiceData();
    }
    resetFormData();
    isActive(false);
  };

  return (
    <div className="card">
      <div className="card-header d-flex align-items-center justify-content-between ps-3">
        Create Service
        <div className="action-group">
          <button
            type="button"
            className="btn btn-light"
            onClick={() => {
              isActive(false);
            }}
          >
            <i className="bi bi-x-lg"></i>
          </button>
        </div>
      </div>
      <div className="card-body">
        <form onSubmit={submit}>
          <input
            type="text"
            className="form-control mb-3"
            placeholder="Title"
            name="title"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
          />
          <input
            type="text"
            className="form-control mb-3"
            placeholder="Icon html"
            name="icon"
            value={icon}
            onChange={(e) => setIcon(e.target.value)}
          />

          <ImageUpload onInput={(data) => setImage(data)} errorMessage="Please upload an image"/>

          <textarea
            className="form-control mb-3"
            rows="3"
            placeholder="Description"
            name="description"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
          ></textarea>
          <button
            className="btn btn-primary"
            type="submit"
            disabled={!(title && icon)}
          >
            Add
          </button>
        </form>
        {isLoading && (
          <div className="alert alert-primary mt-3" role="alert">
            Loading...
          </div>
        )}
        {errorMsg && (
          <div className="alert alert-danger mt-3" role="alert">
            {errorMsg}
          </div>
        )}
      </div>
    </div>
  );
}
