import { useState, useEffect, Fragment } from "react";

export default function ImageUpload({ onInput, errorMessage }) {
  const [file, setFile] = useState();
  const [imagePrev, setImagePrev] = useState();
  //const [isValid, setIsValid] = useState(false);

  useEffect(() => {
    if (file) {
      const fileReader = new FileReader();
      fileReader.readAsDataURL(file);
      fileReader.onload = () => {
        setImagePrev(fileReader.result);
      };
    }
  }, [file]);

  const imageHandler = (event) => {
    let selectedFile;
    //let fileIsValid = isValid;
    const files = event.target.files;
    if (files && files.length === 1) {
      selectedFile = files[0];
      setFile(selectedFile);
      //setIsValid(true);
      // fileIsValid = true;
    } /* else {
     setIsValid(false);
     fileIsValid = false;
    } */
    onInput(selectedFile);
  };

  return (
    <Fragment>
      <label className={`btn-file-upload mb-3 ${imagePrev && "is-active"}`}>
        <input
          type="file"
          className="d-none"
          accept=".jpg,jpeg,.png"
          name="image"
          onChange={imageHandler}
        />
        {!imagePrev && <div className="label">Please pick an image.</div>}
        {imagePrev && <img src={imagePrev} alt="" />}
      </label>

      {/* {!isValid && (
        <div className="alert alert-danger mt-3" role="alert">
          {errorMessage}
        </div>
      )} */}
    </Fragment>
  );
}
